.logo {
  height: 100%;
  padding-top: 0.5rem;
}
.logo > svg {
  display: block;
}

.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  /* box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37); */
  background: #fefbf8;
  max-width: 1500px;
  border-bottom: 0.5px solid black;
}

.img--splash-nav {
  height: 3rem;
  position: absolute;
  top: 0.5rem;
  right: 2rem;
  display: none;
}

.navbar > ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0.5rem 3rem;
}

.navbar--items {
  display: flex;
  justify-content: space-evenly;
  font-size: 1.3rem;
}
.navbar--items a {
  padding-left: 2rem;
  font-family: 'Poiret One', cursive;
  font-weight: 500;
  /* display: inline-block; */
  /* position: relative; */
}

